.SA-Map {
  position: relative;
  width: 40%;
  height: 100%;
  background-color: #20254b;
  border-radius: 10px;
  overflow: hidden;
}

.SA-Map figure {
  /* margin: -5em; */
  display: flex;
  width: 100%;
  /* padding: 1em; */
  opacity: 0.25;
}

.SA-Map figure img {
  width: 100%;
  margin: auto;
}

.SA-Map > h4 {
  font-size: 1.25rem;
  position: absolute;
  top: 1.25em;
  left: 1.5em;
}

.SA-Map footer {
  position: absolute;
  bottom: 2em;
  left: 2em;
}

.SA-Map footer h4 {
  color: #007a4d;
}

.SA-Map footer h3 {
  margin: 0.3em 0 0.8em;
}

.SA-Map footer button {
  color: #fff;
  padding: 0.5em 0.8em;
  border: 1px solid #ffb612;
  background-color: #ffb612;
  border-radius: 35px;
}

.statistics-map {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  padding: 2em 8em;
  border-radius: 10px;
  background-color: #1b1c3a;
}

.statistics-map button {
  color: #fff;
  width: 140px;
  height: 36px;
  margin-bottom: 1em;
  background-color: #20254b;
  border: 1px solid #20254b;
  border-radius: 35px;
  box-shadow: 0 0 25px rgba(0, 0, 0, 0.1);
}

.statistics-map .map {
  display: flex;
  width: 70%;
  height: 90%;
  /* background-color: #20254b; */
  border-radius: 10px;
}

.statistics-map .map figure {
  width: 80%;
  height: 100%;
}

.statistics-map .visualization {
  position: absolute;
  top: 1.5em;
  right: 8em;
  width: 30%;
}

.statistics-map .visualization .Card header {
  position: relative;
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 45px;
  background-color: #20254b;
  padding: 0 1em;
  z-index: 1;
}

.statistics-map .visualization .Card .stat {
  display: none;
  justify-content: space-between;
  width: 100%;
  color: #007a4d;
  padding: 1em 2em;
  background-color: rgba(255, 255, 255, 0.01);
}

.statistics-map .visualization .Card header {
  border-bottom: 1px solid rgba(0, 0, 0, 0.05);
}

.statistics-map .visualization .Card header h3,
.statistics-map .visualization .Card header img {
  margin: auto 0;
}

.statistics-map .visualization .Card header img {
  width: 18px;
}

.statistics-map .visualization .Card:nth-child(1) header {
  border-radius: 10px 10px 0 0;
}

.statistics-map .visualization .Card:last-of-type header {
  border-radius: 0 0 10px 10px;
}

.statistics-map .visualization .Card header h3 span:first-of-type {
  text-align: center;
  font-size: 0.875rem;
  display: inline-block;
  width: 32px;
  height: 32px;
  margin-right: 0.5em;
  padding: 0.7em 0;
  background-color: #007a4d;
  border-radius: 50%;
}

.statistics-map .visualization .Card:nth-child(1) header h3 span:first-of-type {
  background-color: #de3831;
}

.statistics-map .visualization .Card:nth-child(2) header h3 span:first-of-type {
  background-color: #a6e7ff;
}

.statistics-map .visualization .Card:nth-child(3) header h3 span:first-of-type {
  background-color: #007a4d;
}

.statistics-map .visualization .Card:nth-child(4) header h3 span:first-of-type {
  background-color: #ffb612;
}

.statistics-map .visualization .Card:nth-child(5) header h3 span:first-of-type {
  background-color: #ff878d;
}

.statistics-map .visualization .Card:nth-child(6) header h3 span:first-of-type {
  background-color: #a6a6a6;
}

.statistics-map .visualization .Card:nth-child(7) header h3 span:first-of-type {
  background-color: #6eaea1;
}

.statistics-map .visualization .Card:nth-child(8) header h3 span:first-of-type {
  color: #20254b;
  background-color: #fff;
}

.statistics-map .visualization .Card:nth-child(9) header h3 span:first-of-type {
  background-color: #0a7e8c;
}

.statistics-map
  .visualization
  .Card:nth-child(10)
  header
  h3
  span:first-of-type {
  background-color: #1b1c3a;
}

.statistics-map .visualization .Card:nth-child(1) .stat {
  display: flex;
}

.statistics-map .visualization .Card .stat ul {
  color: #fff;
  width: 48%;
  margin: auto 0;
  padding-top: 0.5em;
}

.statistics-map .visualization .Card .stat ul li {
  font-weight: 600;
  margin-bottom: 0.5em;
}

.statistics-map .visualization .Card .stat ul span:first-of-type {
  color: #007a4d;
  margin-right: 0.5em;
}

.statistics-map .visualization .Card .stat .graph {
  position: relative;
  display: flex;
  width: 45%;
}

.statistics-map .visualization .Card .stat .graph h4 {
  text-align: center;
  font-size: 0.875rem;
  color: #fff;
  position: absolute;
  top: 2.5em;
  left: 1.5em;
}

.statistics-map .visualization .Card .stat .graph h4 span {
  color: #ffb612;
  display: block;
}

.statistics-map .visualization .Card .stat .provincechart svg {
  display: none;
}

.statistics-map .visualization .Card .stat .provincechart svg:last-of-type {
  display: inline-block;
  width: 100%;
}

@media (max-width: 50em) {
  .statistics-map {
    width: 100%;
    padding: 7em 2em 0;
    height: auto;
  }

  .statistics-map .map {
    width: 100%;
    height: auto;
  }

  .statistics-map .map figure {
    width: 100%;
  }

  .statistics-map .map img {
    width: 100%;
    height: auto;
  }

  .statistics-map .visualization {
    position: relative;
    left: 0;
    top: 0;
    width: 405px;
    margin: 3em auto;
  }
}

@media (max-width: 30em) {
  .statistics-map {
    position: fixed;
    height: 100vh;
    padding: 3em 1em 0;
    overflow: hidden;
    overflow-y: scroll;
    overflow: -moz-scrollbars-none;
    -ms-overflow-style: none;
    scrollbar-width: none;
  }

  .statistics-map::-webkit-scrollbar {
    display: none;
  }

  .statistics-map .visualization {
    width: 100%;
  }

  .statistics-map .visualization .Card .stat {
    padding: 1em;
  }

  .statistics-map .visualization .Card .stat .graph h4 {
    font-size: 0.75rem;
    top: 2.7em;
    left: 1.6em;
  }
}
