.Dashboard .overview .National-LineChart {
  /* display: flex; */

  width: 58.5%;
  height: 100%;
  background-color: #20254b;
  border-radius: 10px;
}

.Dashboard .overview .National-LineChart .linechart {
  width: 100%;
  /* margin: 0.8em auto 0; */
  /* padding-left: 1em; */
}

.Dashboard .overview .National-LineChart .linechart svg {
  display: none;
}

.Dashboard .overview .National-LineChart .linechart svg:last-of-type {
  display: inline-block;
}

/* AXES */
/* ticks */
.Dashboard .overview .National-LineChart .axis line {
  /* stroke: #706f6f; */
  stroke-width: 0.5;
  shape-rendering: crispEdges;
}

/* axis contour */
.Dashboard
  .overview
  .National-LineChart
  .axis
  .Dashboard
  .overview
  .National-LineChart
  path {
  stroke: #706f6f;
  stroke-width: 0.7;
  shape-rendering: crispEdges;
}

/* axis text */
.Dashboard .overview .National-LineChart .axis text {
  /* fill: #2b2929; */
  /* font-family: Georgia; */
  font-size: 1rem;
}

.Dashboard .overview .National-LineChart #linechart .axis text {
  font-size: 0.75rem;
}

/* LINE CHART */
.Dashboard .overview .National-LineChart path {
  fill: none;
  stroke: #fff;
}

/* LINE CHART */
.Dashboard .overview .National-LineChart .provincial-stats path.line-0 {
  fill: none;
  stroke: #de3831;
}

.Dashboard .overview .National-LineChart path.line-1 {
  fill: none;
  stroke: #a6e7ff;
  /* stroke-dasharray: 2; */
}

.Dashboard .overview .National-LineChart path.line-2 {
  fill: none;
  stroke: rgb(0, 122, 77);
  /* stroke-dasharray: 6; */
}

.Dashboard .overview .National-LineChart path.line-3 {
  fill: none;
  stroke: #ffb612;
  /* stroke-dasharray: 6; */
}

.Dashboard .overview .National-LineChart path.line-4 {
  fill: none;
  stroke: #ff878d;
  /* stroke-dasharray: 6; */
}

.Dashboard .overview .National-LineChart path.line-5 {
  fill: none;
  stroke: #a6a6a6;
}

.Dashboard .overview .National-LineChart path.line-6 {
  fill: none;
  stroke: #6eaea1;
}

.Dashboard .overview .National-LineChart path.line-7 {
  fill: none;
  stroke: #fff;
}

.Dashboard .overview .National-LineChart path.line-8 {
  stroke: #0a7e8c;
}

/* .Dashboard .overview .National-LineChart path.line-3 {
  fill: none;
  stroke: #706f6f;

} */

.serie_label {
  fill: #fff;
  /* font-family: Georgia; */
  font-size: 80%;
}

.Dashboard .overview .National-LineChart .provincial-stats {
  position: relative;
  display: flex;
  justify-content: space-between;
  height: 100%;
}

.Dashboard .overview .National-LineChart header {
  display: flex;
  justify-content: space-between;
  width: 90%;
  font-size: 0.75rem;
  margin: 1em 2em 0;
  padding: 0;
}

.Dashboard .overview .National-LineChart header ul {
  background-color: transparent;
  border: none;
}

.Dashboard .overview .National-LineChart header ul li {
  cursor: pointer;
  color: #fff;
  display: inline-block;
  margin: 0 0.5em;
  padding: 0.5em 0.8em;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  border-radius: 15px;
}

.Dashboard .overview .National-LineChart header select {
  padding: 0.5em 0.8em;
  border: none;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
}

/* .Dashboard .overview .National-LineChart header button span:first-of-type {
  background-color: #1b1c3a;
} */

.Dashboard .overview .National-LineChart header label {
  display: flex;
}

.Dashboard .overview .National-LineChart header label > span {
  display: flex;
  color: #fff;
  margin: 0 0.5em;
  padding: 0.5em 0.8em;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  /* border-radius: 15px; */
}

.Dashboard .overview .National-LineChart header label > span span {
  display: inline-block;
  margin: auto 0;
}

.Dashboard .overview .National-LineChart header input {
  height: 16px;
}

.Dashboard .overview .National-LineChart .provincial-stats svg {
  display: none;
}

.Dashboard .overview .National-LineChart .provincial-stats svg:last-of-type {
  display: inline-block;
}

/* .Dashboard .overview .National-LineChart .provincial-stats .linechart {
  width: 100%;
} */

.Dashboard .overview .National-LineChart .provincial-stats ul {
  font-size: 0.675rem;
  width: 80px;
  height: 80%;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  margin-top: 1.5em;
  margin-left: 1em;
  /* margin-top: 1em; */
}

.Dashboard .overview .National-LineChart .provincial-stats ul span {
  display: inline-block;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: #fff;
}

@media (max-width: 50em) {
  .Dashboard .overview .National-LineChart {
    width: 100%;
    margin-bottom: 1em;
  }
}

@media (max-width: 30em) {
  .Dashboard .overview .National-LineChart {
    overflow: hidden;
    overflow-x: scroll;
  }
  .Dashboard .overview .National-LineChart header {
    width: 92%;
    font-size: 0.75rem;
    margin: 1em auto 0;
  }

  .Dashboard .overview .National-LineChart header label > span {
    display: none;
  }

  .Dashboard .overview .National-LineChart header select {
    padding: 0.3em 0.5em;
  }

  .Dashboard .overview .National-LineChart .linechart,
  .Dashboard .overview .National-LineChart .provincial-stats {
    width: 750px;
  }

  .Dashboard .overview .National-LineChart .provincial-stats svg {
    width: 95%;
    /* height: 300px; */
  }

  /* .Dashboard .overview .National-LineChart .linechart svg {
    height: 400px;
  } */

  .Dashboard .overview .National-LineChart .provincial-stats {
    margin-top: -1em;
  }

  .Dashboard .overview .National-LineChart .provincial-stats ul {
    width: 45px;
    margin: 1em 0 0;
  }
}
