.Dashboard {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  color: #fff;
  width: 100%;
  height: 100vh;
  background-color: #1b1c3a;
  padding: 1em 8em;
}

.Dashboard .logo {
  position: absolute;
  top: 4em;
  left: 2em;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  z-index: 100;
}

.Dashboard .overview {
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 46vh;
  flex-wrap: wrap;
}

.Dashboard .overview header {
  display: flex;
  justify-content: space-between;
  width: 58.5%;
  margin-bottom: 1em;
  padding: 0 1em;
}

.Dashboard .overview header h2 {
  font-size: 1.25rem;
}

.Dashboard .overview header select {
  color: #fff;
  background-color: transparent;
  border: 1px solid rgba(255, 255, 255, 0.25);
  padding-right: 0.8em;
  /* height: ; */
}

.Dashboard .overview header select option {
  color: #000;
}
.Dashboard .breakdown {
  display: flex;
  justify-content: space-between;
  height: 40vh;
  margin-top: 1em;
}

@media (max-width: 50em) {
  .Dashboard {
    padding: 2em;
    overflow: auto;
  }

  .Dashboard .logo {
    top: 2em;
  }

  .Dashboard .overview {
    flex-direction: column;
    flex-wrap: nowrap;
    height: auto;
    margin-top: 5em;
  }

  .Dashboard .overview header {
    width: 100%;
  }

  .Dashboard .breakdown {
    height: auto;
  }

  .Dashboard .breakdown .SA-Map {
    display: none;
    /* position: absolute;
    right: 2em;
    display: inline-block;
    width: 37%;
    height: 237px;
    margin-top: -253px; */
  }
}

@media (max-width: 30em) {
  .Dashboard {
    padding: 1em;
  }
}
