.Provincial-Table {
  width: 58.5%;
  height: 100%;
}

.Provincial-Table ul {
  display: flex;
  justify-content: space-between;
  padding: 1em;
  border-bottom: 1px solid #20254b;
}

.Provincial-Table > ul {
  background-color: #20254b;
  border-radius: 10px;
  height: 17.8%;
}

.Provincial-Table > ul li {
  margin: auto 0;
}

.Provincial-Table div {
  height: 65%;
  overflow-x: scroll;
  overflow: -moz-scrollbars-none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.Provincial-Table div::-webkit-scrollbar {
  display: none;
}

.Provincial-Table div ul {
  color: rgba(255, 255, 255, 0.25);
}

.Provincial-Table ul li:first-of-type {
  color: #007a4d;
  width: 30%;
}

.Provincial-Table ul li:last-of-type {
  text-align: right;
}

.Provincial-Table ul li {
  width: 17.5%;
}

.Provincial-Table div ul:last-of-type {
  border-bottom: none;
}

.Provincial-Table ul li:last-of-type {
  color: #ffb612;
}

@media (max-width: 50em) {
  .Provincial-Table {
    width: 100%;
    height: auto;
  }

  .Provincial-Table > ul {
    height: auto;
  }

  .Provincial-Table div {
    height: auto;
  }
}

@media (max-width: 30em) {
  .Provincial-Table {
    overflow: hidden;
    overflow-x: scroll;
  }

  .Provincial-Table > ul,
  .Provincial-Table div {
    width: 600px;
  }
}
