.Overrall-Stats {
  width: 40%;
}

.Overrall-Stats .stats {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 100%;
  height: 100%;
}

.Overrall-Stats .stats .Card div {
  display: flex;
}

.Overrall-Stats .stats .Card div .donutchart {
  margin-top: 0;
  /* margin-bottom: 1em; */
}

.Overrall-Stats .stats .Card div h3 {
  width: 90px;
  text-align: center;
  margin: auto 0;
  margin-right: -4.6em;
}

.Overrall-Stats .stats .Card div h3 span {
  display: block;
  font-size: 1rem;
}

.Overrall-Stats .stats .Card div h3 span:last-of-type {
  color: rgba(255, 255, 255, 0.2);
  font-size: 0.75rem;
  margin-top: 0.2em;
}

.Overrall-Stats .stats .Card {
  position: relative;
  /* width: 55%; */
  padding: 1em;
  border-radius: 10px;
  background-color: #20254b;
}

.Overrall-Stats .stats .Card .donutchart {
  width: 100%;
  margin-top: 1em;
}

.Overrall-Stats .stats .Card .donutchart svg text {
  font-size: 2rem;
}

.Overrall-Stats .stats .Card footer {
  position: absolute;
  bottom: 2em;
}

.Overrall-Stats .stats .Card img {
  height: 18px;
}

.Overrall-Stats .stats .Card h5 {
  /* color: rgba(255, 255, 255, 0.25); */
  color: #007a4d;
  margin: 0.5em 0;
}

.Overrall-Stats .stats .Card h4 {
  color: #fff;
}

.Overrall-Stats .stats > .Card {
  width: 47%;
  height: auto;
  padding: 2em;
}

.Overrall-Stats .stats > div:last-of-type {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 50%;
}

.Overrall-Stats .stats > div:last-of-type .Card {
  height: 90px;
}

.Overrall-Stats .SA-Map,
.Overrall-Stats .statistics-map {
  display: none;
}

@media (max-width: 50em) {
  .Overrall-Stats {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }

  .Overrall-Stats .stats {
    width: 58%;
  }

  .Overrall-Stats .stats > div:last-of-type .Card {
    height: 30%;
    padding-top: 0.5em;
  }

  .Overrall-Stats .stats .Card h5 {
    margin: 0.2em 0;
  }

  .Overrall-Stats .SA-Map {
    width: 40%;
    display: inline-block;
  }

  .Overrall-Stats .statistics-map {
    display: inline-block;
    width: 100%;
  }

  .Dashboard .overview .statistics-map header {
    margin: 0;
  }
}

@media (max-width: 30em) {
  .Overrall-Stats {
    flex-direction: column;
  }

  .Overrall-Stats .stats {
    width: 100%;
    height: auto;
  }

  .Overrall-Stats .stats > .Card {
    display: flex;
    width: 100%;
    height: 130px;
    margin-bottom: 1em;
    padding: 1em 2em;
  }

  .Overrall-Stats .stats .Card div h3 {
    margin-right: -4.9em;
  }

  .Overrall-Stats .stats > .Card svg {
    width: 140px;
  }

  .Overrall-Stats .stats > .Card footer {
    position: relative;
    padding: auto 0;
    margin: 1em 0;
    bottom: 0;
  }

  .Overrall-Stats .stats > div:last-of-type {
    width: 100%;
  }

  .Overrall-Stats .stats > div:last-of-type .Card {
    height: 90px;
    padding: 1em 2em;
    margin-bottom: 1em;
  }

  .Overrall-Stats .SA-Map {
    width: 100%;
    height: 300px;
  }

  /* .Dashboard .overview .statistics-map {
    padding-top: 7em;
  } */
}
